import React, { useEffect, useState } from "react";

const Statewise = () => {

    const [data, setData] = useState([]);
    
  const getCovidData = async () => {
    const response = await fetch("https://api.covid19india.org/data.json");
    const actualData = await response.json();
    //   console.log(actualData.statewise);
      setData(actualData.statewise);
  };

  useEffect(() => {
    getCovidData();
  }, []);
  return (
    <div>
      <div className="container mt-5">
        <div className="main-heading">
          <h1 className="mb-5 text-center">INDIA COVID-19 DATA</h1>
        </div>

        <div className="table-responsive">
          <table className="table table-light table-bordered table-hover  shadow-sm p-3 mb-5 bg-white rounded">
            <thead className="table-dark">
              <tr>
                <th>State</th>
                <th>Confirmed</th>
                <th>Recovered</th>
                <th>Deaths</th>
                <th>Active</th>
                <th>Update</th>
              </tr>
            </thead>

            <tbody>
                          {data.map((currentEle, index) => {
                              return (
                                  <tr key={index}>
                                    <td>{currentEle.state}</td>
                                    <td>{currentEle.confirmed}</td>
                                    <td>{currentEle.recovered}</td>
                                    <td>{currentEle.deaths}</td>
                                    <td>{currentEle.active}</td>
                                    <td>{currentEle.lastupdatedtime}</td>
                                </tr>
                             ) 
                          })}
               
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default Statewise;
